import Vue from "vue";
import Router from "vue-router";

import Menu from "./components/Menu/index.vue";
import Error from "./components/Error/404.vue";

import Title from "./components/Menu/Title/Index.vue";
import ListNew from "./components/Menu/Product/ListNew.vue";
import NewProduct from "./components/Menu/Product/AddNewProduct.vue";
import InStockProduct from "./components/Menu/Product/AddInStock.vue";
import ListProduct from "./components/Menu/Product/Index.vue";
import Reward from "./components/Menu/Reward/Index.vue";
import AddGroupReward from "./components/Menu/Reward/AddGroupReward.vue";
import GroupReward from "./components/Menu/Reward/GroupReward.vue";
import AddReward from "./components/Menu/Reward/AddReward.vue";
import Raffle from "./components/Menu/Raffle/Index.vue";
import GetLocation from "./components/Menu/Raffle/GetLocation.vue";

Vue.use(Router);

const router = new Router({
  mode: "history",
  routes: [
    {
      path: "/gundamnew",
      component: Menu,
      meta: { text: "Menu" },
      children: [
        {
          path: "/gundamnew/title",
          component: Title,
          meta: { text: "Tiêu đề đề" },
          name: "title",
        },
        {
          path: "/gundamnew/list-new",
          component: ListNew,
          meta: { text: "Hàng Sắp Về" },
          name: "list-new-product",
        },
        {
          path: "/gundamnew/new-product",
          component: NewProduct,
          meta: { text: "Thêm Mới Hàng Sắp Về" },
          name: "add-new-product",
        },
        {
          path: "/gundamnew/add-product",
          component: InStockProduct,
          meta: { text: "Thêm Sản Phẩm" },
          name: "add-product",
        },
        {
          path: "/gundamnew",
          component: ListProduct,
          meta: { text: "Danh Sách Sản Phẩm" },
          name: "list-product",
        },
        {
          path: "/gundamnew/group-reward",
          component: Reward,
          meta: { text: "Danh Sách Giải Thưởng Offine" },
          name: "reward",
        },
        {
          path: "/gundamnew/add-group-reward",
          component: AddGroupReward,
          meta: { text: "Thêm Giải Thưởng Offine" },
          name: "add-reward",
        },
        {
          path: "/gundamnew/group-reward1",
          component: GroupReward,
          meta: { text: "Danh Sách Giải Thưởng Group" },
          name: "group-reward",
        },
        {
          path: "/gundamnew/add-group-reward1",
          component: AddReward,
          meta: { text: "Thêm Giải Thưởng Group" },
          name: "group-reward1",
        },
        {
          path: "/gundamnew/raffle",
          component: Raffle,
          meta: { text: "Raffle" },
          name: "raffle",
        },
        {
          path: "/gundamnew/location",
          component: GetLocation,
          meta: { text: "Get Location" },
          name: "getLocation",
        },
      ],
    },
    { path: "gundamnew/404", name: "errorpage", component: Error }, //token, user
    // { path: '*', redirect: '/404' }
  ],
});

export default router;
