<template>
  <div>
    <div>
      <img v-if="!image_upload" src="./../../../assets/empty-img.png" />
      <img
        v-else
        :src="image_upload"
        alt=""
        @click="getCoordinates"
        ref="image"
      />
    </div>
    <input accept="image/jpeg" type="file" ref="upload" @change="showImg" />
    <p>X: {{ x }}</p>
    <p>Y: {{ y }}</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      image_upload: null,
      x: 0,
      y: 0,
    };
  },
  methods: {
    showImg(e) {
      this.image_upload = URL.createObjectURL(e.target.files[0]);
    },
    getCoordinates(event) {
      const rect = this.$refs.image.getBoundingClientRect();
      this.x = event.clientX - rect.left;
      this.y = event.clientY - rect.top;
    },
  },
};
</script>

