<template>
  <div>
    <div class="form-raffle">
      <el-form
        :model="data"
        :rules="rules"
        ref="raffleForm"
        label-width="120px"
      >
        <el-form-item label="Tọa độ X 1" prop="xdata1">
          <el-input v-model="data.xdata1"></el-input>
        </el-form-item>
        <el-form-item label="Tọa độ Y 1" prop="ydata1">
          <el-input v-model="data.ydata1"></el-input>
        </el-form-item>
        <el-form-item label="Tọa độ X 2" prop="xdata2">
          <el-input v-model="data.xdata2"></el-input>
        </el-form-item>
        <el-form-item label="Tọa độ Y 2" prop="ydata2">
          <el-input v-model="data.ydata2"></el-input>
        </el-form-item>
        <el-form-item label="Font Size" prop="size">
          <el-input v-model="data.size"></el-input>
        </el-form-item>
        <el-form-item label="Chọn khoản">
          <el-col :span="11">
            <el-form-item label="Từ" prop="to">
              <el-input v-model.number="data.to" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col class="line" :span="2">-</el-col>
          <el-col :span="11">
            <el-form-item label="Đến" prop="from">
              <el-input
                v-model.number="data.from"
                autocomplete="off"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-form-item>

        <el-form-item label="Màu sắc" prop="colorText">
          <span class="demonstration">{{ data.colorText }}</span>
          <el-color-picker v-model="data.colorText"></el-color-picker>
        </el-form-item>

        <el-form-item label="Hình ảnh">
          <div class="wrapper-img">
            <img src="./../../../assets/empty-img.png" />
            <input
              accept="image/jpeg"
              type="file"
              class="file-upload"
              id="fileupload-slide"
              ref="upload"
              @change="showImg"
            />
          </div>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('raffleForm')"
            >Tạo</el-button
          >
          <el-button @click="resetForm('raffleForm')">Reset</el-button>
        </el-form-item>
      </el-form>
    </div>
    <br />
    <br />
    <div ref="capture" class="image-container">
      <img v-if="!image_upload" src="@/assets/raffle/raffle.jpg" alt="Raffle" />
      <img v-else :src="image_upload" alt="" />
      <div
        class="number-overlay"
        :style="{
          top: data.xdata1 + 'px',
          left: data.ydata1 + 'px',
          color: data.colorText,
          fontSize: data.size + 'px',
        }"
      >
        {{ currentNumber }}
      </div>
      <div
        class="number-overlay"
        :style="{
          top: data.xdata2 + 'px',
          left: data.ydata2 + 'px',
          color: data.colorText,
          fontSize: data.size + 'px',
        }"
      >
        {{ currentNumber }}
      </div>
    </div>
  </div>
</template>

<script>
import html2canvas from "html2canvas";
export default {
  data() {
    return {
      currentNumber: "01",

      image_upload: "",
      data: {
        to: 1,
        from: 50,
        colorText: "",
        xdata1: 160,
        ydata1: 514,
        xdata2: 160,
        ydata2: 914,
        size: 35,
      },
      rules: {
        to: [{ required: true, message: "Từ số là bắt buộc", trigger: "blur" }],
        from: [
          { required: true, message: "Đến số là bắt buộc", trigger: "blur" },
        ],
        colorText: [
          { required: true, message: "Màu sắc là bắt buộc", trigger: "change" },
        ],
        xdata1: [
          { required: true, message: "X1 số là bắt buộc", trigger: "blur" },
        ],
        ydata1: [
          { required: true, message: "Y1 số là bắt buộc", trigger: "blur" },
        ],
        xdata2: [
          { required: true, message: "X2 số là bắt buộc", trigger: "blur" },
        ],
        ydata2: [
          { required: true, message: "Y2 số là bắt buộc", trigger: "blur" },
        ],
        size: [
          { required: true, message: "Size số là bắt buộc", trigger: "blur" },
        ],
      },
    };
  },

  methods: {
    async captureImages() {
      for (let number = this.data.to; number <= this.data.from; number++) {
        this.currentNumber = number;
        await this.$nextTick(); // Chờ cập nhật DOM
        const canvas = await html2canvas(this.$refs.capture);
        const link = document.createElement("a");
        link.download = `raffle_with_number_${number}.jpeg`;
        link.href = canvas.toDataURL("image/jpeg", 0.9);
        link.click();
      }
    },
    showImg(e) {
      this.image_upload = URL.createObjectURL(e.target.files[0]);
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    submitForm(formName) {
      if (!this.$refs.upload.files[0] || !this.image_upload) {
        this.$notify.error({
          title: "Lỗi",
          message: "Chưa nhập hình",
        });
        return;
      }
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.captureImages();
        } else {
          return false;
        }
      });
    },
  },
};
</script>

<style scoped>
@import url("https://fonts.googleapis.com/css2?family=Archivo+Black&display=swap");
.image-container {
  position: relative;
  display: inline-block;
}

.number-overlay {
  position: absolute;
  font-family: "Archivo Black", sans-serif;
  font-weight: 400;
  font-style: normal;
}

img {
  display: block;
}

.form-raffle {
  background: #f5f5f5;
  padding: 30px;
  width: 600px;
  border: 2px dashed #d9d9d9;
}
</style>
