<template>
    <div class="wrap">
      <el-form :model="item" :rules="rules" ref="item">
        <el-form-item label="Mã Số" prop="code" :label-width="formLabelWidth">
          <el-input v-model.number="item.code" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="Giải" prop="name" :label-width="formLabelWidth">
          <el-input v-model.number="item.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="Hình ảnh" :label-width="formLabelWidth">
          <div class="wrapper-img">
            <img v-if="!image_upload" src="./../../../assets/empty-img.png" />
            <img v-else :src="image_upload" alt="" />
            <input
              accept="image/jpeg"
              type="file"
              class="file-upload"
              id="fileupload-slide"
              ref="upload"
              @change="showImg"
            />
          </div>
        </el-form-item>  
        <el-form-item>
          <el-button type="primary" @click="submitForm('item')">Tạo</el-button>
          <el-button @click="resetForm('item')">Reset</el-button>
        </el-form-item>
      </el-form>
    </div>
  </template>
  
  <script>
  import { storage } from "../../../storage";
  import GroupReward from "../../../services/GroupReward";
  export default {
    data() {
      return {
        data: [],
        image_upload: "",
        item: {
          id: null,
          code: null,
          name: 0,
          ImgUrl: "",
        },
  
        rules: {
          name: [
            { type: "number", message: "Vui lòng nhập số" },
          ],
          code: [
            { required: true, message: "Vui lòng Mã Số" },
            { type: "number", message: "Vui lòng nhập số" },
          ],
        },
  
        formLabelWidth: "120px",
      };
    },
    methods: {
      async submitForm(formName) {
        if (!this.$refs.upload.files[0] || !this.image_upload) {
          this.$notify.error({
            title: "Lỗi",
            message: "Chưa nhập hình",
          });
  
          return;
        }
        await this.uploadImage();
        await this.$refs[formName].validate((valid) => {
          if (valid) {
            var values = Object.values(this.data);
            if (values.length !== 0) {
              this.item.id = values[values.length - 1].id + 1;
            } else {
              this.item.id = 1;
            }
  
            GroupReward.update(this.item.id, this.item)
              .then(() => {
                this.$notify({
                  title: "Hoàn tất",
                  message: "Lưu thành công",
                  type: "success",
                });
  
                this.resetForm("item");
              })
              .catch((e) => {
                this.$notify.error({
                  title: "Lỗi",
                  message: e,
                });
              });
          } else {
            console.log("error submit!!");
            return false;
          }
        });
      },
      async uploadImage() {
        let ImgToUpload = this.$refs.upload.files[0];
        const metaData = {
          contentType: ImgToUpload.type,
        };
        const storageRef = storage.ref(`gr-reward/`);
        const imageRef = storageRef.child(ImgToUpload.name);
  
        await imageRef.put(ImgToUpload, metaData);
  
        const downloadURL = await imageRef.getDownloadURL();
        this.item.ImgUrl = downloadURL;
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
        this.$refs.upload.value = "";
        this.image_upload = "";
      },
      showImg(e) {
        this.image_upload = URL.createObjectURL(e.target.files[0]);
      },
      onDataChange(items) {
        let _data = [];
  
        items.forEach((item) => {
          let data = item.val();
          _data.push({
            id: data.id,
            code: data.code,
            name: data.name,
            ImgUrl: data.ImgUrl,
          });
        });
  
        this.data = _data;
      },
      getAll() {
        GroupReward.getAll().on("value", this.onDataChange);
      },
    },
    mounted() {
      this.getAll();
    },
  };
  </script>
  
  <style lang="css" scoped>
  .wrap {
    background: #f5f5f5;
    padding: 30px;
    width: 600px;
  }
  .left {
    text-align: left;
  }
  
  .wrappe-image {
    height: inherit;
  }
  
  .wrapper-img {
    height: 200px;
    width: 200px;
    position: relative;
    border: 2px dashed #d9d9d9;
    border-radius: 5px;
    overflow: hidden;
    z-index: 1;
  }
  
  .wrapper-img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .wrapper-img .default {
    position: absolute;
    top: -50%;
    left: -50%;
    transform: translate(-50%, -50%);
    background: #fff;
  }
  
  .file-upload {
    position: absolute;
    bottom: 0px;
    outline: none;
    color: transparent;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    cursor: pointer;
    transition: 0.5s;
    z-index: 2;
    display: block;
  }
  
  .file-upload::-webkit-file-upload-button {
    visibility: hidden;
  }
  </style>
  