<template>
  <div>
    <div class="set-left">
      <router-link to="/gundamnew/add-group-reward1">
        <el-button type="primary" icon="el-icon-edit">Thêm</el-button>
      </router-link>
    </div>
    <el-table :data="data" style="width: fit-content" height="600px">
      <el-table-column prop="id" label="ID" width="120"> </el-table-column>
      <el-table-column prop="name" label="Giải" width="120"> </el-table-column>
      <el-table-column prop="ImgUrl" label="Hình" width="300" align="center">
        <template slot-scope="scope">
          <img class="img" :src="scope.row.ImgUrl" alt="" />
        </template>
      </el-table-column>
      <el-table-column fixed="right" width="200" align="center">
        <template slot-scope="scope">
          <el-button
            @click="prepareDelete(scope.row.id)"
            type="danger"
            icon="el-icon-delete"
            size="small"
            >Xóa</el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <el-dialog title="Cảnh báo" :visible.sync="dialogDelete" width="30%">
      <span>Bạn có muốn xóa sản phẩm này?!</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogDelete = false">Không</el-button>
        <el-button type="danger" @click="handleDelete">Có</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import GroupReward from "@/services/GroupReward.js";
export default {
  data() {
    return {
      data: [],
      dialogDelete: false,
      deleteItem: null,
    };
  },
  methods: {
    getAll() {
      GroupReward.getAll().on("value", this.onDataChange);
    },
    onDataChange(items) {
      let _data = [];

      items.forEach((item) => {
        let data = item.val();
        _data.push({
          id: data.id,
          name: data.name,
          ImgUrl: data.ImgUrl,
        });
      });

      this.data = _data;
    },
    prepareDelete(key) {
      this.dialogDelete = true;
      this.deleteItem = key;
    },
    handleDelete() {
      if (this.deleteItem == null) {
        return;
      }
      this.dialogDelete = true;
      GroupReward.delete(this.deleteItem)
        .then(() => {
          this.dialogDelete = false;
          this.$notify({
            title: "Hoàn tất",
            message: "Xóa thành công",
            type: "success",
          });
          this.getAll();
          this.deleteItem = null;
        })
        .catch((e) => {
          this.$notify.error({
            title: "Lỗi",
            message: e,
          });
        });
    },
  },
  mounted() {
    this.getAll();
  },
};
</script>

<style scoped>
.set-left {
  text-align: left;
}
</style>
